import Autocomplete from '@mui/material/Autocomplete';
import TextField from '@mui/material/TextField';

const UserPlaylists = (props) => {
    let playlistNames = []
    props.userPlaylists.map(playlist => (playlistNames.push(playlist.name)))

    const onChange = (event, value) => {
        props.setPlaylistToAdapt(value)
    }

    return (
        <Autocomplete
            disablePortal
            onChange={onChange}
            id="user-playlists"
            options={playlistNames}
            sx={{ width: 300 }}
            renderInput={(params) => <TextField {...params} label="Your Playlists" />}
    />
    )
    
    

}

export default UserPlaylists;