import '../App.css';
import {useEffect, useState} from 'react';
import axios from 'axios';


import NavBar from '../components/NavBar.js';
import Genres from '../components/Genres.js';
import Artists from '../components/Artists.js';
import TrackList from '../components/TrackList.js';
import AlbumArt from '../components/AlbumArt';
import UserPlaylists from '../components/UserPlaylists';
import PlaylistInput from '../components/PlaylistInput';

import ButtonGroup from '@mui/material/ButtonGroup';
import Button from '@mui/material/Button';

import LibraryMusicIcon from '@mui/icons-material/LibraryMusic';
import PlaylistAddCheckIcon from '@mui/icons-material/PlaylistAddCheck';
import WrapReport from './WrapReport';

function Home() {
  const CLIENT_ID = "f7e73d8d950b4b5cb859b86c15d45075"
  const REDIRECT_URI = "http://localhost:3000"
  const AUTH_ENDPOINT = "https://accounts.spotify.com/authorize"
  const RESPONSE_TYPE = "token"
  const SCOPE = "user-top-read, playlist-modify-public, playlist-modify-private"

  const [token, setToken] = useState("")
  const [userInfo, setUserInfo] = useState({})
  const [topTracks, setTopTracks] = useState([])
  const [adaptedTracks, setAdaptedTracks] = useState([])
  const [artistToRemove, setArtistToRemove] = useState("")
  const [genreToRemove, setGenreToRemove] = useState("")
  const [genreInfo, setGenreInfo] = useState([])
  const [genreList, setGenreList] = useState([])
  const [coverUrlList, setCoverUrlList] = useState([])
  const [userPlaylists, setUserPlaylists] = useState({})
  const [playlistToAdapt, setPlaylistToAdapt] = useState('')
  const [playlistIDToAdapt, setPlaylistIDToAdapt] = useState('')
  const [userAudioFeatures, setUserAudioFeatures] = useState([])
  const [playlistName, setPlaylistName] = useState('')
  const [page, setPage] = useState('dashboard')
  const [createdPlaylistData, setCreatedPlaylistData] = useState({})


  //authorization

  useEffect( () => {
    const hash = window.location.hash
    let token = window.localStorage.getItem("token")

    if (!token && hash) {
      token = hash.substring(1).split("&").find(elem => elem.startsWith("access_token")).split("=")[1]
      window.location.hash = ""
      window.localStorage.setItem("token", token)
    }
    setToken(token)
    
  }, [])

  //get userID 

  useEffect( () => {
    const getUserID = async() => {

      if(token) {
        const {data} = await axios.get("https://api.spotify.com/v1/me", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })

        let currentUserInfo = {
          name: data.display_name,
          first_name: data.display_name.substring(0, data.display_name.indexOf(' ')),
          img: data.images[0].url,
          uri: data.uri,
          id: data.id
        }
        setUserInfo(currentUserInfo)
      }
    }
    getUserID()
  }, [token])

  useEffect(() => {
    getExistingPlaylists()
  }, [userInfo, token])

  const login = () => {
    window.location=`${AUTH_ENDPOINT}?client_id=${CLIENT_ID}&scope=${SCOPE}&redirect_uri=${REDIRECT_URI}&response_type=${RESPONSE_TYPE}`
  }

  const logout = () => {
    setToken("")
    window.localStorage.removeItem("token")
    setTopTracks([])
    setAdaptedTracks([])
    setUserInfo({})
    setCoverUrlList([])
    setUserPlaylists([])
    setUserAudioFeatures([])
    setPlaylistToAdapt('')
    setPlaylistIDToAdapt('')
    setPage('home')
    setCreatedPlaylistData({})
  }

  //fetch data from api

  const getTopTracksMedium = async () => {
    const {data} = await axios.get("https://api.spotify.com/v1/me/top/tracks?limit=50", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      params: {
        time_range: "medium_term"
      }
    })
    setTopTracks(data.items)
    setCreatedPlaylistData({
      name: "Your Adapted Top Songs",
      description: 'Spotify WrapAdapt presents an adapted version of the songs that you loved most in the last 6 months'
    })
    cleanTopTracks()
  }

  const getTopTracksShort= async () => {
    const {data} = await axios.get("https://api.spotify.com/v1/me/top/tracks?limit=50", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      params: {
        time_range: "short_term"
      }
    })
    setTopTracks(data.items)
    setCreatedPlaylistData({
      name: "Your Adapted Top Songs",
      description: 'Spotify WrapAdapt presents an adapted version of the songs that you loved most in the last 4 weeks'
    })
    cleanTopTracks()
  }

  const getTopTracksLong = async () => {
    const {data} = await axios.get("https://api.spotify.com/v1/me/top/tracks?limit=50", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      params: {
        time_range: "long_term"
      }
    })
    setTopTracks(data.items)
    setCreatedPlaylistData({
      name: "Your Adapted Top Songs",
      description: 'Spotify WrapAdapt presents an adapted version of the songs that you loved most in the last few years'
    })
    cleanTopTracks()
  }

  const getExistingPlaylists = async () => {
    const id = userInfo.id
    const {data} = await axios.get(`https://api.spotify.com/v1/users/${id}/playlists?limit=50`, {
      headers: {
        Authorization: `Bearer ${token}`,
      }
    })
    setUserPlaylists(data.items)
  }

  const getAudioFeatures = async (uri) => {
    const {data} = await axios ({
        method:"get",
        url: `https://api.spotify.com/v1/audio-features/${uri}`,
        headers: {
          Authorization: `Bearer ${token}`,
        },
    })
    addToAudioFeaturesList(data)
  }

  const addToAudioFeaturesList = (trackAudioFeatures) => {
    let newUserAudioFeatures = userAudioFeatures
    newUserAudioFeatures.push(trackAudioFeatures)
    setUserAudioFeatures(newUserAudioFeatures)
  }

  const getTracksInPlaylist = async () => {

    // set playlistID to playlistIDToAdapt
    // If playlist selected via input box, this will be the actual playlist id
    // if playlist selected via dropdown, this will be ''
    let playlistID = playlistIDToAdapt

    // check if playlistIDToAdapt is ''. if yes, set it to playlist id
    if(playlistIDToAdapt == ''){
      playlistID = userPlaylists.find(playlist => playlist.name === playlistToAdapt).id}

    const {data} = await axios.get(`https://api.spotify.com/v1/playlists/${playlistID}/tracks`, {
      headers: {
        Authorization: `Bearer ${token}`,
      }
    })
    console.log(data);
    let tracks = []

    // data structure is slightly different for private vs public playlists
    // get tracks and name from each
    let playlistName = ''
    if (data.items) {
      data.items.map(track => tracks.push(track.track))
      playlistName = playlistToAdapt;
    } else {
      data.tracks.items.map(track => tracks.push(track.track))
      playlistName = data.name;
    }

    setTopTracks(tracks)
    setCreatedPlaylistData({
      name: `Adapted ${playlistName}`,
      description: `Spotify WrapAdapt presents an adapted version of ${playlistName}`
    })
    setPlaylistName(playlistName)
    cleanTopTracks()
  }

  const getGenre = async (artist) => {
    const {data} = await axios.get("https://api.spotify.com/v1/search", {
      headers: {
        Authorization: `Bearer ${token}`
      },
      params: {
        q: artist,
        type: "artist"
      }
    })

    let genre = data.artists.items[0].genres
    addToGenreList(genre)

    let newGenreInfo = {
      artist_id: data.artists.items[0].id,
      genres: data.artists.items[0].genres
    }
    await setGenreInfo(genreInfo.push(newGenreInfo))
    
  }

  // modify top tracks list

  const cleanTopTracks = () => {
    let adapted = []
    topTracks.forEach((track, index) => adapted.push({
      ranking: index+1,
      track_id: track.id,
      name: track.name,
      artist: track.artists[0].name,
      artist_id: track.artists[0].id,
      album: track.album.name,
      album_img: track.album.images[0].url,
      genre: []

    }))

    adapted.forEach(track => {
      getGenre(track.artist)
    })

    setTimeout(()=> {
      //forEach track, set track.artist to genre
      adapted.forEach(track => {
        track.genre = genreInfo.find(genre => genre.artist_id == track.artist_id).genres
      });
    },"2000")

    setAdaptedTracks(adapted)

    //push album covers to list of covers
    const coverUrlList = []
    adapted.forEach(track => { coverUrlList.push(track.album_img)})
    setCoverUrlList(coverUrlList);

  }


  const setRankings = (tracks) => {
    tracks.forEach((track, index) => track.ranking = index+1)
  }

  const removeByArtist = () => {
    const filtered = adaptedTracks.filter(track => (track.artist != artistToRemove))
    setRankings(filtered)
    setAdaptedTracks(filtered)

  }

  const removeByGenre = () => {
    const filtered = adaptedTracks.filter(track => !(track.genre.includes(genreToRemove)))
    setRankings(filtered)
    setAdaptedTracks(filtered)

    const filteredGenreList = genreList.filter(genre => (genre!=genreToRemove))
    setGenreList(filteredGenreList)
  }

  // modify genres list

  const addToGenreList = (artistGenreList) => {
    let newGenreList = genreList
    artistGenreList.map(singleGenre => !newGenreList.includes(singleGenre)? newGenreList.push(singleGenre):'')
    setGenreList(newGenreList)
  }

  const wrapFacts = () => {
    let uris = []
    adaptedTracks.map(track => {
        uris.push(track.track_id)
    })

    uris.forEach(uri => {getAudioFeatures(uri)})
    console.log(userAudioFeatures);
    let sum = userAudioFeatures.length;
    let audioFeatureSummary = {
      acousticness: 0,
      danceability: 0,
      energy: 0,
      loudness: 0,
      mode: 0,
      tempo: 0,
      valence: 0,
    }
    userAudioFeatures.forEach(track => 
      {audioFeatureSummary.acousticness+=track.acousticness
      audioFeatureSummary.danceability+=track.danceability
      audioFeatureSummary.energy+=track.energy
      audioFeatureSummary.loudness+=track.loudness
      audioFeatureSummary.mode+=track.mode
      audioFeatureSummary.tempo+=track.tempo
      audioFeatureSummary.valence+=track.valence})

    audioFeatureSummary.acousticness/= sum
    audioFeatureSummary.danceability/= sum
    audioFeatureSummary.energy/= sum
    audioFeatureSummary.loudness/= sum
    audioFeatureSummary.mode/= sum
    audioFeatureSummary.tempo/= sum
    audioFeatureSummary.valence/= sum

    console.log(audioFeatureSummary)
    
    let page = 'home'
    page = userAudioFeatures[0].acousticness ? 'report' : 'home'
    setPage(page)
  }
  

  


  // api calls for making new playlist
  const makePlaylist = async () => {
   const {data} = await axios ({
      method:"post",
      url: `https://api.spotify.com/v1/users/${userInfo.id}/playlists`,
      headers: {
        Authorization: `Bearer ${token}`,
      },
      data: {
        "name": `${createdPlaylistData.name}`,
        "description":`${createdPlaylistData.description}`,
        "public": true
      }

    })

   addToPlaylist(data.id)
  }

  const addToPlaylist = async(playlistID) => {
    let uris = []
    adaptedTracks.map(track => {
      uris.push(`spotify:track:${track.track_id}`)
    })
    await axios({
      method: "put",
      url: `https://api.spotify.com/v1/playlists/${playlistID}/tracks`,
      headers: {
        Authorization: `Bearer ${token}`,
      },
      data: {
        uris: uris
      }
    })
  }

  return (
    <div className='home'>
        <div className='navbar-container'>
          <NavBar login={login} logout={logout} userInfo={userInfo}/>
        </div>

        {token && adaptedTracks.length==0 ? <div className='timeframe-options'>
                        <h2 className='timeframe-header'>adapt your top tracks in the last</h2>
                        <ButtonGroup>
                          <Button variant="outlined" 
                          startIcon={<LibraryMusicIcon />}
                           onClick={getTopTracksShort}>
                              4 Weeks

                          </Button> 
                          <Button variant="outlined" 
                          startIcon={<LibraryMusicIcon />}
                          onClick={getTopTracksMedium}>
                              6 Months

                          </Button> 
                          <Button variant="outlined" 
                          startIcon={<LibraryMusicIcon />}
                          onClick={getTopTracksLong}>
                              Several Years

                          </Button> 

                        </ButtonGroup> 
                        </div>: ''}

        {token && adaptedTracks.length==0 && userPlaylists.length>=0? <div className='add-playlist-options'>
          <h2> adapt an existing playlist </h2>
          <UserPlaylists userPlaylists={userPlaylists} setPlaylistToAdapt={setPlaylistToAdapt}/>
          <PlaylistInput setPlaylistIDToAdapt={setPlaylistIDToAdapt}></PlaylistInput> 
          <Button variant="outlined" onClick={getTracksInPlaylist}>Adapt</Button>
        </div>:''}

        
      {adaptedTracks.length>=1 && page=== 'dashboard' ?
      <div className='dashboard'>
        <div className='filters'>
          <h2>Adapt Your Tracks</h2>
          <Genres genreList={genreList}
                  removeByGenre={removeByGenre}
                  setGenreToRemove={setGenreToRemove}/>
          <Artists adaptedTracks={adaptedTracks}
                   removeByArtist={removeByArtist}
                   setArtistToRemove={setArtistToRemove}/>
        </div>

        <div className='tracks-container'>
          <div className='playlist-header'>
          {playlistName.length<1 ? 
          <h2> Your Top {adaptedTracks.length} Songs </h2> : 
          <h2>{playlistName} ({adaptedTracks.length} songs)</h2>}
            
          </div>
          <TrackList adaptedTracks={adaptedTracks}/>
          
          <div className='playlist-button-container'>
  
            <Button className='make-playlist-button'
                  variant="contained"
                  startIcon={<PlaylistAddCheckIcon />}
                  onClick={makePlaylist}>Make Playlist On Spotify</Button>
            <Button className='more-info-button'
                  variant="contained"
                  onClick={wrapFacts}>Get more info</Button>
          </div>
        </div>

      </div> : '' }

      {page==='report' ? <WrapReport 
                          userAudioFeatures={userAudioFeatures}
                          adaptedTracks={adaptedTracks}
                          /> : ''}
      
    </div>
  );
}

export default Home;