import '../App.css';
import { Radar, RadarChart, PolarGrid, 
    PolarAngleAxis, PolarRadiusAxis } from 'recharts';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import { Typography } from '@mui/material';

const WrapReport = (props) => {
    // radar chart of features
    // top 5 songs
     
    const top5Tracks = props.adaptedTracks.slice(0, 5)

    let top5SongNames = []
    top5Tracks.forEach(track => top5SongNames.push(track.name))
    
    let top5SongImgs = []
    top5Tracks.forEach(track => top5SongImgs.push(track.album_img))

    let genres = []
    props.adaptedTracks.forEach(track => genres.push(track.genre))
    const flattenedGenres = [].concat.apply([],genres)
    console.log(flattenedGenres)

    const getTopGenres = (genreList) => {
        let counts = {}
        for (const n of genreList) {
            counts[n] = counts[n] ? counts[n] + 1 : 1;
        }
        //convert counts to array
        counts = Object.keys(counts).map((key) => [key, counts[key]]);
        //sort by count
        counts.sort((a, b) => b.count - a.count);
        // return top 5
        counts = counts.slice(0, 5);

        let top5Genres = [];

        counts.forEach(genre => top5Genres.push(genre[0]))
        return(top5Genres)

    };

    const top5Genres = getTopGenres(flattenedGenres)

    const features = props.userAudioFeatures[0]

    // inverse acoustic so 1 is most acoustic
    const acousticness = 1 - features.acousticness
    const danceability = features.danceability
    const energy = features.energy
    const mode = features.mode
    const valence = features.valence

    // radar chart of features
    const data = [
        { name: 'Acousticness', x: acousticness },
        { name: 'Danceability', x: danceability },
        { name: 'Energy', x: energy },
        { name: 'Mode', x: mode },
        { name: 'Valence', x: valence },
    ];

    // card component to display top song img and name
    const ItemCard = (props) => {
        return(
            <Card >
                 <CardContent sx={{ display: 'flex', flexDirection: 'row'}}>
                     {props.type==='song' ?
                     <CardMedia 
                     component='img'
                     sx={{ width: 50, pb: 0, pr: 1}}
                     image={props.imgdata[props.index]}
                     alt='album cover'/> : ''
                     }
                    
                    <Typography>#{props.index+1} {props.data[props.index]}</Typography>
                </CardContent>
            </Card>
        )
    }


    //link to open in spotify
    return (
        <div>
            <h2 className='summary-header'>WrapAdapt Summary</h2>
            <div className='report-container'>
            
                <RadarChart height={500} width={500} 
                    outerRadius="65%" data={data}>
                    <PolarGrid />
                    <PolarAngleAxis dataKey="name" tick={{ fill: 'white'}} />
                    <PolarRadiusAxis />
                    <Radar dataKey="x" 
                        fill="#4caf50" fillOpacity={.9} />
                </RadarChart>
                <div className='top5-container'>
                    <Box sx={{ width: 400, height: 400}}>
                        <Typography gutterBottom variant='h5'>Top Songs</Typography>
                        <ItemCard index={0} type='song' imgdata={top5SongImgs} data={top5SongNames}/>
                        <ItemCard index={1} type='song' imgdata={top5SongImgs} data={top5SongNames}/>
                        <ItemCard index={2} type='song' imgdata={top5SongImgs} data={top5SongNames}/>
                        <ItemCard index={3} type='song' imgdata={top5SongImgs} data={top5SongNames}/>
                        <ItemCard index={4} type='song' imgdata={top5SongImgs} data={top5SongNames}/>
                    </Box>
                </div>
                <div className='top5-container'>
                    <Box sx={{ width: 250, height: 400}}>
                        <Typography gutterBottom variant='h5'>Top Genres</Typography>
                        <ItemCard index={0} type='genre' data={top5Genres}/>
                        <ItemCard index={1} type='genre' data={top5Genres}/>
                        <ItemCard index={2} type='genre' data={top5Genres}/>
                        <ItemCard index={3} type='genre' data={top5Genres}/>
                        <ItemCard index={4} type='genre' data={top5Genres}/>

                    </Box>
                </div> 
            </div>
        </div>
        
    )

}

export default WrapReport;
