import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';

const TrackList = (props) => {
    const columns = [
        { id: 'index', label: '#', minWdith: 100},
        { id: 'title', label: 'TITLE', midWidth: 100},
        { id: 'artist', label: 'ARTIST', midWidth: 100},
        { id: 'album', label: 'ALBUM', midWidth: 100},
        { id: 'image', label: '', minWidth: 100}
    ];

    function createData(track) {
        let data = {
            index: track.ranking,
            title: track.name,
            artist: track.artist,
            album: track.album,
            image: <img 
                    src={track.album_img} 
                    alt='album art'
                    width='50'
                    height='50'/>
        }
        return data
    }

    let rows = [];
    props.adaptedTracks.forEach(track=> rows.push(createData(track)))

    return (
        <Paper sx={{ width: 750, overflow: 'hidden' }}>
            <TableContainer sx={{ maxHeight: 500 }}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows
                        .map((row) => {
                            return (
                                <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                                    {columns.map((column) => {
                                        const value = row[column.id];
                                        return (
                                            <TableCell key={column.id} align={column.align}>
                                                {column.format && typeof value === 'number'
                                                ? column.format(value)
                                                : value}
                                            </TableCell>
                                        );
                                    })}
                                </TableRow>
                            );
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
        </Paper>
    );

}

export default TrackList;