//renders autocomplete dropdown and button to remove by artist
import { useState } from 'react';

import Filter from './Filter.js';

function Artists(props) {
    const allSongsArtistList = []
    props.adaptedTracks.map(track => allSongsArtistList.push(track.artist))

    //get list of distinct artist names
    const distinct = (value, index, self) => {
        return self.indexOf(value) === index;
    }
    const artistList = allSongsArtistList.filter(distinct)

    const [value, setValue] = useState(null)
    const onChange = (event, value) => {
        props.setArtistToRemove(value)
    }
    const handleButtonClick = () => {
        props.removeByArtist();
        setValue(null);
    }

    return (
        <Filter 
        onChange={onChange}
        value={value}
        id="artists"
        options={artistList}
        inputLabel="Artist to Remove"
        handleButtonClick={handleButtonClick}
        disabled={!value}/>
        
    );
}

export default Artists;